export default class Card {
  constructor ({data, handleCardClick, handleTrashClick}, templateSelector) {
    this._placeImage = data.link;
    this._placeTitle = data.name;
    this._likes = data.likes;
    this._id = data._id;
    this._ownerCardID = data.owner._id;
    this._templateSelector = templateSelector;
    this._handleCardClick = handleCardClick;
    this._handleTrashClick = handleTrashClick;
  }

  //создаие карточки
  _getTemplate () {
    return document
      .querySelector(this._templateSelector)
      .content.querySelector('.place__item')
      .cloneNode(true); //клонирование карточки
  }

  //содержимое карточки
  createCard () {
    this._element = this._getTemplate();

    this._imageCard = this._element.querySelector('.place__img');
    this._imageCard.src = this._placeImage;
    this._imageCard.alt = this._placeTitle;
    this._element.querySelector('.place__title').textContent = this._placeTitle;
    this._element.querySelector('.place__like').textContent = this._likes.length;

    this._trashButton = this._element.querySelector('.place__trash')
    if (this._ownerCardID != '689701b98af447c10c8e0f86') {
      this._trashButton.classList.add('place__trash_none');
    }

    this._likeElementCard = this._element.querySelector('.like-button');

    this._setEventListeners();

    return this._element;
  }

  _setEventListeners () {
    this._likeElementCard.addEventListener('click', () => {
      this._handleLikeClick();
    });

    this._trashButton.addEventListener('click', () => {
      this._handleTrashClick(this._element, this._id);
    });

    this._imageCard.addEventListener('click', () => {
      this._handleCardClick (this._placeImage, this._placeTitle);
    });
  }

  //лайк
  _handleLikeClick() {
    this._likeElementCard.classList.toggle('like-button_active');
  }

  //удаление карточки
/*   deleteCard(elementCard) {
    elementCard.remove();
    elementCard = null;
  } */
}

